// react
import { useEffect, useState } from 'react';
// component
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
// MUI
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import Stack from '@mui/material/Stack';
// api
import useGetClass from 'src/api/controllers/class';
import useGetSubject from 'src/api/controllers/subject';
import useGetTranscript from 'src/api/controllers/transcript';
// route
import { paths } from 'src/routes/paths';
// utils
import { fDateTime } from 'src/utils/format-time';
// table
import TableData from '../../table/table-data';
// context
import { useTableContext } from '../../table/context';
import TranscriptForm from './stranscript-form';
import { TableHeader, TableRowCollapse, TableRows } from './transcript-table-item';

// ----------------------------------------------------------------------

export default function AllSchoolView() {
  const {
    _table_data,
    _table_open_form,
    onSetOpenForm,
    //
    onSetTableData,
    onSetTableHeader,
    onSetTableRows,
    onSetTableRowCollapse,
    onSetImportTableData,
    onSetExportTableData,
    onSetConfigTable,
  } = useTableContext();

  const { data: classes } = useGetClass();

  const { data: subjects } = useGetSubject();

  const { data: transcripts } = useGetTranscript();

  const [subjectId, setSubjectId] = useState();

  const [gradeId, setGradeId] = useState();

  const [classId, setCLassId] = useState();

  const filterGrade = subjects?.filter((subject: any) => subject?.id === subjectId)[0]?.grades;

  const filterClass = classes?.filter((classroom: any) => classroom?.grade?.id === gradeId);

  const filterTranscript = transcripts?.filter(
    (transcript: any) => transcript?.class?.id === classId
  );

  useEffect(() => {
    onSetTableData(
      filterTranscript?.map((transcript: any) => ({
        ...transcript,
        type: transcript?.subject?.name,
      }))
    );

    onSetImportTableData({
      sample_data: [],
      option_field: null,
      option_values: [],
    });

    onSetConfigTable({
      table_name: 'transcript',
      add_new: !!subjectId,
      //
      filter_by_date: true,
      filter_by_type: true,
      filter_by_search: true,
      //
      import_data: false,
      export_data: true,
      //
      table_selected: true,
      allocation_student: false,
      delete_all: true,
      collapse_item: true,
      //
      edit_row: true,
      delete_row: true,
    });

    onSetTableHeader(TableHeader);
    onSetTableRows(TableRows);
    onSetTableRowCollapse(TableRowCollapse);
  }, [transcripts, subjectId, classId]);

  useEffect(() => {
    onSetExportTableData(
      _table_data?.map((transcript: any) => ({
        'Họ tên học sinh': transcript?.student?.name,
        'Thuộc lớp': `Lớp ${transcript?.class?.grade?.label}${transcript?.class?.name}`,
        'Thuộc môn': transcript?.subject?.name,
        'Điểm thường xuyên 1 (HKI)': transcript?.t1S1,
        'Điểm thường xuyên 2 (HKI)': transcript?.t1S2,
        'Điểm thường xuyên 3 (HKI)': transcript?.t1S3,
        'Điểm thường xuyên 4 (HKI)': transcript?.t1S4,
        'Điểm thường xuyên 5 (HKI)': transcript?.t1S5,
        'Điểm thi giữa kỳ (HKI)': transcript?.t1Mid,
        'Điểm thi cuối kỳ (HKI)': transcript?.t1Final,
        'Điểm trung bình học kỳ (HKI)': transcript?.t1Overall,
        'Điểm thường xuyên 1 (HKII)': transcript?.t2S1,
        'Điểm thường xuyên 2 (HKII)': transcript?.t2S2,
        'Điểm thường xuyên 3 (HKII)': transcript?.t2S3,
        'Điểm thường xuyên 4 (HKII)': transcript?.t2S4,
        'Điểm thường xuyên 5 (HKII)': transcript?.t2S5,
        'Điểm thi giữa kỳ (HKII)': transcript?.t2Mid,
        'Điểm thi cuối kỳ (HKII)': transcript?.t2Final,
        'Điểm trung bình học kỳ (HKII)': transcript?.t2Overall,
        'Điểm trung bình cả năm': transcript?.final,
        'Ngày tạo': fDateTime(transcript?.createdAt),
        'Ngày cập nhật': fDateTime(transcript?.updatedAt),
      }))
    );
  }, [_table_data]);

  return (
    <>
      <CustomBreadcrumbs
        heading="Quản lý điểm học sinh"
        links={[
          { name: 'Danh mục' },
          {
            name: 'Bảng điểm',
            href: paths.home.transcript.root,
          },
          { name: 'Quản lý điểm' },
        ]}
        sx={{ mb: { xs: 3, md: 5 } }}
      />

      <Stack direction="row" sx={{ px: 3, pb: 3 }} spacing={2}>
        <FormControl>
          <InputLabel>Lựa chọn môn học...</InputLabel>
          <Select
            label="Lựa chọn môn học..."
            value={subjectId}
            onChange={(event: any) => setSubjectId(event.target.value)}
            sx={{ width: 300 }}
          >
            {subjects?.map((option: any) => (
              <MenuItem key={option?.id} value={option?.id}>
                {option?.name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>

        <FormControl>
          <InputLabel>Lựa chọn khối lớp...</InputLabel>
          <Select
            disabled={!subjectId}
            label="Lựa chọn khối lớp..."
            value={gradeId}
            onChange={(event: any) => setGradeId(event.target.value)}
            sx={{ width: 300 }}
          >
            {filterGrade?.map((option: any) => (
              <MenuItem key={option?.id} value={option?.id}>
                {`Khối lớp ${option?.label}`}
              </MenuItem>
            ))}
          </Select>
        </FormControl>

        <FormControl>
          <InputLabel>Lựa chọn lớp học...</InputLabel>
          <Select
            disabled={!gradeId || !subjectId}
            label="Lựa chọn lớp học..."
            value={classId}
            onChange={(event: any) => setCLassId(event.target.value)}
            sx={{ width: 300 }}
          >
            {filterClass?.map((option: any) => (
              <MenuItem key={option?.id} value={option?.id}>
                {`Lớp ${option?.grade?.label}${option?.name}`}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </Stack>

      <TableData />

      <TranscriptForm
        open={_table_open_form}
        onClose={() => onSetOpenForm(false)}
        classId={classId}
        subjectId={subjectId}
        students={filterClass && filterClass[0]?.students}
      />
    </>
  );
}
