// component
import { CustomInfoBar } from 'src/components/custom-info-bar';
import Label from 'src/components/label';
// mui
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import TableCell from '@mui/material/TableCell';
import Typography from '@mui/material/Typography';

// ----------------------------------------------------------------------

function Classification(diem: number) {
  switch (true) {
    case diem >= 8 && diem <= 10:
      return 'Học lực giỏi';
    case diem >= 6.5 && diem < 8:
      return 'Học lực khá';
    case diem >= 5 && diem < 6.5:
      return 'Học lực trung bình';
    case diem >= 3.5 && diem < 5:
      return 'Học lực yếu';
    case diem < 3.5:
      return 'Học lực kém';
    default:
      return 'Điểm không hợp lệ';
  }
}

export const TableHeader = [
  { id: 'student', label: 'Tên học sinh', flex: 1 },

  { id: 'final', label: 'Điểm cả năm', flex: 1, align: 'center' },
  { id: 'classification', label: 'Xếp loại', flex: 1, align: 'center' },
  { id: 'createdAt', label: 'Ngày tạo', flex: 1, align: 'center' },
  { id: 'updatedAt', label: 'Ngày cập nhật', flex: 1, align: 'center' },
  { id: 'details', label: 'Danh sách điểm chi tiết', flex: 1, align: 'center' },
  { id: 'action', label: 'Hành động', flex: 1, align: 'right' },
];

export const TableRows = (row: any) => {
  return (
    <>
      <TableCell>
        <CustomInfoBar primary={row?.student?.name} secondary={row?.subject?.name} />
      </TableCell>

      <TableCell align="center">Điểm TB : {Number(row?.final)?.toFixed(2)}</TableCell>

      <TableCell align="center">
        <Label variant="filled" color="info" sx={{ textTransform: 'uppercase' }}>
          {Classification(Number(row?.final))}
        </Label>
      </TableCell>
    </>
  );
};

export const TableRowCollapse = (row: any) => {
  return (
    <Stack direction="row" spacing={5} component={Paper} sx={{ m: 1.5, padding: 1 }}>
      <Stack direction="row" spacing={2} alignItems="center">
        <Typography variant="subtitle2">Điểm HKI :</Typography>
        <Stack spacing={0.5} alignItems="center">
          <Typography variant="caption">Điểm thường xuyên</Typography>
          <Stack direction="row" spacing={1}>
            <Label variant="filled">{row?.t1S1}</Label>
            <Label variant="filled">{row?.t1S2}</Label>
            <Label variant="filled">{row?.t1S3}</Label>
            <Label variant="filled">{row?.t1S4}</Label>
            <Label variant="filled">{row?.t1S5}</Label>
          </Stack>
        </Stack>

        <Stack spacing={0.5} alignItems="center">
          <Typography variant="caption">Điểm thi</Typography>
          <Stack direction="row" spacing={1}>
            <Label variant="filled" color="info">
              {row?.t1Mid}
            </Label>
            <Label variant="filled" color="info">
              {row?.t1Final}
            </Label>
          </Stack>
        </Stack>

        <Stack spacing={0.5} alignItems="center">
          <Typography variant="caption">Tổng kết</Typography>
          <Stack direction="row" spacing={1}>
            <Label variant="filled" color="error">
              {Number(row?.finalp1).toFixed(2)}
            </Label>
          </Stack>
        </Stack>
      </Stack>

      <Stack direction="row" spacing={2} alignItems="center">
        <Typography variant="subtitle2">Điểm HKII :</Typography>
        <Stack spacing={0.5} alignItems="center">
          <Typography variant="caption">Điểm thường xuyên</Typography>
          <Stack direction="row" spacing={1}>
            <Label variant="filled">{row?.t2S1}</Label>
            <Label variant="filled">{row?.t2S1}</Label>
            <Label variant="filled">{row?.t2S1}</Label>
            <Label variant="filled">{row?.t2S1}</Label>
            <Label variant="filled">{row?.t2S1}</Label>
          </Stack>
        </Stack>

        <Stack spacing={0.5} alignItems="center">
          <Typography variant="caption">Điểm thi</Typography>
          <Stack direction="row" spacing={1}>
            <Label variant="filled" color="info">
              {row?.t2Mid}
            </Label>
            <Label variant="filled" color="info">
              {row?.t2Final}
            </Label>
          </Stack>
        </Stack>

        <Stack spacing={0.5} alignItems="center">
          <Typography variant="caption">Tổng kết</Typography>
          <Stack direction="row" spacing={1}>
            <Label variant="filled" color="error">
              {Number(row?.finalp2).toFixed(2)}
            </Label>
          </Stack>
        </Stack>
      </Stack>
    </Stack>
  );
};
