// react
import { useEffect, useMemo } from 'react';
// form
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import * as Yup from 'yup';
// @mui
import LoadingButton from '@mui/lab/LoadingButton';
import Alert from '@mui/material/Alert';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Divider from '@mui/material/Divider';
import Grid from '@mui/material/Grid';
import MenuItem from '@mui/material/MenuItem';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
// components
import FormProvider, { BlockItem, RHFSelect, RHFTextField } from 'src/components/hook-form';
import { useSnackbar } from 'src/components/snackbar';
// api
import { createTranscript, updateTranscript } from 'src/api/controllers/transcript';
// context
import { useTableContext } from '../../table/context';

// ----------------------------------------------------------------------

type Props = {
  open: boolean;
  onClose: VoidFunction;
  classId: any;
  subjectId: any;
  students: any;
};

const totalScore = (
  score1: string,
  score2: string,
  score3: string,
  score4: string,
  score5: string,
  mid_score: string,
  final_score: string
) => {
  const factor =
    [
      score1,
      score2,
      score3,
      score4,
      score5,
      mid_score,
      mid_score,
      final_score,
      final_score,
      final_score,
    ]?.filter((item) => item !== '')?.length || 1;

  const total =
    Number(score1) +
    Number(score2) +
    Number(score3) +
    Number(score4) +
    Number(score5) +
    Number(mid_score) * 2 +
    Number(final_score) * 3;

  return Number(total / factor).toFixed(2);
};

export default function TranscriptForm({ open, onClose, classId, subjectId, students }: Props) {
  const {
    _table_row_selected: currentValues,
    onSetTableRowSelected,
    onCreateNewRow,
    onUpdateRow,
  } = useTableContext();

  const isEdit = !!currentValues;

  const { enqueueSnackbar } = useSnackbar();

  const Schema = Yup.object().shape({
    student: Yup.string().required('Bạn chưa chọn học sinh !'),
    t1S1: Yup.mixed().nullable(),
    t1S2: Yup.mixed().nullable(),
    t1S3: Yup.mixed().nullable(),
    t1S4: Yup.mixed().nullable(),
    t1S5: Yup.mixed().nullable(),
    t1Mid: Yup.mixed().nullable(),
    t1Final: Yup.mixed().nullable(),

    t2S1: Yup.mixed().nullable(),
    t2S2: Yup.mixed().nullable(),
    t2S3: Yup.mixed().nullable(),
    t2S4: Yup.mixed().nullable(),
    t2S5: Yup.mixed().nullable(),
    t2Mid: Yup.mixed().nullable(),
    t2Final: Yup.mixed().nullable(),

    finalp1: Yup.mixed().nullable(),
    finalp2: Yup.mixed().nullable(),
    final: Yup.mixed().nullable(),
  });

  const defaultValues = useMemo(
    () => ({
      student: currentValues?.student?.id || '',
      t1S1: currentValues?.t1S1 || '',
      t1S2: currentValues?.t1S2 || '',
      t1S3: currentValues?.t1S3 || '',
      t1S4: currentValues?.t1S4 || '',
      t1S5: currentValues?.t1S5 || '',
      t1Mid: currentValues?.t1Mid || '',
      t1Final: currentValues?.t1Final || '',

      t2S1: currentValues?.t2S1 || '',
      t2S2: currentValues?.t2S2 || '',
      t2S3: currentValues?.t2S3 || '',
      t2S4: currentValues?.t2S4 || '',
      t2S5: currentValues?.t2S5 || '',
      t2Mid: currentValues?.t2Mid || '',
      t2Final: currentValues?.t2Final || '',

      finalp1: currentValues?.t2S5 || '',
      finalp2: currentValues?.t2Mid || '',
      final: currentValues?.t2Final || '',
    }),
    [currentValues]
  );

  const methods = useForm({
    resolver: yupResolver(Schema),
    defaultValues,
  });

  const {
    reset,
    watch,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  useEffect(() => {
    reset(defaultValues);
  }, [reset, currentValues, defaultValues]);

  const finalp1 = totalScore(
    values.t1S1,
    values.t1S2,
    values.t1S3,
    values.t1S4,
    values.t1S5,
    values.t1Mid,
    values.t1Final
  );

  const finalp2 = totalScore(
    values.t2S1,
    values.t2S2,
    values.t2S3,
    values.t2S4,
    values.t2S5,
    values.t2Mid,
    values.t2Final
  );

  const onSubmit = handleSubmit(async (data: any) => {
    try {
      const formData = {
        student: { connect: { id: +data.student } },
        class: { connect: { id: Number(classId) } },
        subject: { connect: { id: Number(subjectId) } },
        t1S1: Number(data?.t1S1),
        t1S2: Number(data?.t1S2),
        t1S3: Number(data?.t1S3),
        t1S4: Number(data?.t1S4),
        t1S5: Number(data?.t1S5),
        t1Mid: Number(data?.t1Mid),
        t1Final: Number(data?.t1Final),

        t2S1: Number(data?.t2S1),
        t2S2: Number(data?.t2S2),
        t2S3: Number(data?.t2S3),
        t2S4: Number(data?.t2S4),
        t2S5: Number(data?.t2S5),
        t2Mid: Number(data?.t2Mid),
        t2Final: Number(data?.t2Final),

        finalp1: Number(finalp1),
        finalp2: Number(finalp2),
        final: (Number(finalp1) + Number(finalp2) * 2) / 3,
      };

      if (isEdit) {
        const updated = await updateTranscript(formData, currentValues?.id);
        onUpdateRow(updated);
      } else {
        const created = await createTranscript(formData);
        onCreateNewRow(created);
      }

      enqueueSnackbar('Cập nhật dữ liệu thành công !');
      reset();
      handleClose();
    } catch (error) {
      console.log(error);
      enqueueSnackbar('Đã có lỗi xảy ra ! Vui lòng thử lại !', { variant: 'error' });
    }
  });

  const handleClose = () => {
    onSetTableRowSelected(null);
    onClose();
  };

  const checkNumber = (value: any, name: string) => {
    if (Number(value) > 10 || Number.isNaN(Number(value))) {
      enqueueSnackbar('Dữ liệu không hợp lệ !', { variant: 'error' });
    } else {
      // @ts-ignore
      setValue(`${name}`, Number(value), { shouldValidate: true });
    }
  };

  return (
    <Dialog fullWidth maxWidth="lg" open={open} onClose={handleClose}>
      <FormProvider methods={methods} onSubmit={onSubmit}>
        <DialogTitle>
          {isEdit
            ? `Cập nhật bảng điểm học sinh ${currentValues?.user?.prof?.fullName}`
            : 'Thêm dữ liệu bảng điểm mới'}
        </DialogTitle>

        <DialogContent>
          <Alert variant="outlined" severity="info" sx={{ mb: 3 }}>
            Ghi chú : Điểm thường xuyên là điểm kiểm tra miệng, kiểm tra 15p hệ số 1, điểm giữa kỳ
            hệ số 2, điểm cuối kỳ hệ số 3...
          </Alert>

          <Grid container spacing={2}>
            <Grid item xs={12}>
              <BlockItem label="Chọn học sinh :" required>
                <RHFSelect name="student" size="small">
                  {students?.map((student: any) => (
                    <MenuItem key={student?.id} value={student?.id}>
                      {student?.name}
                    </MenuItem>
                  ))}
                </RHFSelect>
              </BlockItem>
            </Grid>

            <Grid item xs={12}>
              <Grid container spacing={2}>
                <Grid item xs={6}>
                  <BlockItem label="Điểm thường xuyên kỳ I :">
                    <Stack direction="row" spacing={1}>
                      <RHFTextField
                        size="small"
                        name="t1S1"
                        onChange={(event: any) => checkNumber(event.target.value, 't1S1')}
                      />

                      <RHFTextField
                        size="small"
                        name="t1S2"
                        onChange={(event: any) => checkNumber(event.target.value, 't1S2')}
                      />

                      <RHFTextField
                        size="small"
                        name="t1S3"
                        onChange={(event: any) => checkNumber(event.target.value, 't1S3')}
                      />

                      <RHFTextField
                        size="small"
                        name="t1S4"
                        onChange={(event: any) => checkNumber(event.target.value, 't1S4')}
                      />

                      <RHFTextField
                        size="small"
                        name="t1S5"
                        onChange={(event: any) => checkNumber(event.target.value, 't1S5')}
                      />
                    </Stack>
                  </BlockItem>
                </Grid>

                <Grid item xs={2}>
                  <BlockItem label="Điểm giữa kỳ I :" required>
                    <RHFTextField
                      size="small"
                      name="t1Mid"
                      onChange={(event: any) => checkNumber(event.target.value, 't1Mid')}
                    />
                  </BlockItem>
                </Grid>

                <Grid item xs={2}>
                  <BlockItem label="Điểm cuối kỳ I :" required>
                    <RHFTextField
                      size="small"
                      name="t1Final"
                      onChange={(event: any) => checkNumber(event.target.value, 't1Final')}
                    />
                  </BlockItem>
                </Grid>

                <Grid item xs={2}>
                  <BlockItem label="Tổng kết kỳ I :" required>
                    <TextField
                      size="small"
                      disabled
                      value={totalScore(
                        values.t1S1,
                        values.t1S2,
                        values.t1S3,
                        values.t1S4,
                        values.t1S5,
                        values.t1Mid,
                        values.t1Final
                      )}
                    />
                  </BlockItem>
                </Grid>
              </Grid>
            </Grid>

            <Grid item xs={12}>
              <Divider sx={{ borderStyle: 'dashed' }} />
            </Grid>

            <Grid item xs={12}>
              <Grid container spacing={2}>
                <Grid item xs={6}>
                  <BlockItem label="Điểm thường xuyên kỳ II :">
                    <Stack direction="row" spacing={1}>
                      <RHFTextField
                        size="small"
                        name="t2S1"
                        onChange={(event: any) => checkNumber(event.target.value, 't2S1')}
                      />

                      <RHFTextField
                        size="small"
                        name="t2S2"
                        onChange={(event: any) => checkNumber(event.target.value, 't2S2')}
                      />

                      <RHFTextField
                        size="small"
                        name="t2S3"
                        onChange={(event: any) => checkNumber(event.target.value, 't2S3')}
                      />

                      <RHFTextField
                        size="small"
                        name="t2S4"
                        onChange={(event: any) => checkNumber(event.target.value, 't2S4')}
                      />

                      <RHFTextField
                        size="small"
                        name="t2S5"
                        onChange={(event: any) => checkNumber(event.target.value, 't2S5')}
                      />
                    </Stack>
                  </BlockItem>
                </Grid>

                <Grid item xs={2}>
                  <BlockItem label="Điểm giữa kỳ II :" required>
                    <RHFTextField
                      size="small"
                      name="t2Mid"
                      onChange={(event: any) => checkNumber(event.target.value, 't2Mid')}
                    />
                  </BlockItem>
                </Grid>

                <Grid item xs={2}>
                  <BlockItem label="Điểm cuối kỳ II :" required>
                    <RHFTextField
                      size="small"
                      name="t2Final"
                      onChange={(event: any) => checkNumber(event.target.value, 't2Final')}
                    />
                  </BlockItem>
                </Grid>

                <Grid item xs={2}>
                  <BlockItem label="Tổng kết kỳ II :" required>
                    <TextField
                      size="small"
                      disabled
                      value={totalScore(
                        values.t2S1,
                        values.t2S2,
                        values.t2S3,
                        values.t2S4,
                        values.t2S5,
                        values.t2Mid,
                        values.t2Final
                      )}
                    />
                  </BlockItem>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </DialogContent>

        <DialogActions>
          <Button variant="outlined" onClick={handleClose}>
            Huỷ bỏ
          </Button>

          <LoadingButton type="submit" color="primary" variant="contained" loading={isSubmitting}>
            {isEdit ? 'Cập nhật dữ liệu' : 'Thêm dữ liệu mới'}
          </LoadingButton>
        </DialogActions>
      </FormProvider>
    </Dialog>
  );
}
